import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import * as GA from '../../utils/analystics';
import Media from '../media';
import routes from '../../routes';
const {Link, Router } = routes;
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const linkStyle = {
  marginRight: 15
};

const SpanI = styled.a`
  ${Media.md`display: none;`};
`;

function ActiveLink(props) {
  const {icon,asPath} = props;
  return (<li>
    <FontAwesomeIcon size="xs" icon={icon} />
    <Link  route="/">
      <SpanI  className={`nav-link ${asPath === '/' && 'active'}`} style={linkStyle}>{props.name}</SpanI>
    </Link>
  </li>);
}

